import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { IonicApp, IonicModule } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage';
import { MyApp } from './app.component';
import { StatusBar } from '@ionic-native/status-bar';
import { MusicControls } from '@ionic-native/music-controls';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Keyboard } from '@ionic-native/keyboard';
import { ColorThiefProvider } from '../providers/color-thief/color-thief';
import { HttpClientModule } from '@angular/common/http';

@NgModule({
  declarations: [MyApp],
  imports: [
    HttpClientModule,
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/menu-list/menu-list.module.ngfactory#MenuListPageModuleNgFactory', name: 'MenuListPage', segment: 'menu-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/menu/menu.module.ngfactory#MenuPageModuleNgFactory', name: 'MenuPage', segment: 'menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search/search.module.ngfactory#SearchPageModuleNgFactory', name: 'SearchPage', segment: 'search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/track-detail/track-detail.module.ngfactory#TrackDetailPageModuleNgFactory', name: 'TrackDetailPage', segment: 'detail/:id', priority: 'low', defaultHistory: ['SearchPage'] }
  ]
}),
    IonicStorageModule.forRoot({
      name: '__startTrack'
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [MyApp],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    MusicControls,
    ColorThiefProvider
  ]
})
export class AppModule {}



